import { Component } from '@angular/core';

@Component({
  selector: 'my-app', // <my-app></my-app>
  template: require('./app.component.html'),
  styles: [require('./app.component.sass')],
})
export class AppComponent {
  title = 'Angular 2';
  url = 'https://my-link.com';
}



