import {
  Component,
  Input,
  OnInit,
  DoCheck,
  OnDestroy,
  OnChanges,
  AfterContentInit,
  AfterContentChecked,
  AfterViewChecked,
  AfterViewInit } from '@angular/core';

@Component({
  selector: 'my-about-test-child',
  template: require('./about-test-child.component.html'),
  styles: [require('./about-test-child.component.scss')]
})
export class AboutTestChildComponent implements
  OnInit,
  OnInit,
  DoCheck,
  OnDestroy,
  OnChanges,
  AfterContentInit,
  AfterContentChecked,
  AfterViewChecked,
  AfterViewInit {

  @Input() setAge: string;
  @Input() name: string;
  @Input() counter: string;

  counterChild = 0;

  constructor() {
    // this.log(`constructor`);
  }

  ngOnInit() {
    // this.log('init');
    // load data from server
  }

  ngDoCheck() {
    // this.log('DoCheck       ');
  }

  ngAfterContentInit() {
    // this.log('AfterContentInit          ');
  }

  ngAfterContentChecked() {
    // this.log('AfterContentChecked     ,     ');
  }

  ngAfterViewInit() {
    // this.log('AfterViewInit     ,     . ');
  }

  ngAfterViewChecked() {
    // this.log('AfterViewChecked        ,    .');
  }

  ngOnDestroy() {
    // call before remove component
    // this.log('ngOnDestroy   ,  .');
  }

  ngOnChanges(changes) {
    // call before ngOnInit and when props changed(only input)

    for (let propName in changes) {
      if (changes.hasOwnProperty(propName)) {
        let chng = changes[propName];
        let cur = JSON.stringify(chng.currentValue);
        let prev = JSON.stringify(chng.previousValue);
        this.log(`${propName}: currentValue = ${cur}, previousValue = ${prev}`);
      }
    }
  }

  log(msg: string) {
    return msg;
    // console.log(msg);
  }
}




